ppreciation)}.score-container table tbody tr td[data-label=Note]{position:relative;padding-bottom:40px;display:flex;justify-content:center;align-items:center}.score-container table tbody tr td[data-label=Note]::after{content:attr(data-appreciation);position:absolute;bottom:10px;left:50%;transform:translateX(-50%);font-size:.9rem;color:hotpink;font-style:italic;width:100%;text-align:center;margin-bottom:30px}.score-container table tbody tr td[data-label=ApprÃ©ciation]{display:none}.score-container .moyenne-finale{padding:10px;margin-top:25px;background:#fff0f5;border:2px solid hotpink;border-radius:12px}.score-container .moyenne-finale .moyenne-text,.score-container .moyenne-finale .moyenne-value{font-size:1rem}.score-container .moyenne-finale .moyenne-text{display:block;margin-bottom:8px;font-size:1.1rem}.score-container .moyenne-finale .moyenne-value{font-size:1.3rem;color:hotpink}}.calculate-button{padding:10px 20px;font-size:18px;background-color:hotpink;color:#fff;border:none;border-radius:25px;cursor:pointer;transition:all .3s ease;font-family:"Comic Sans MS",cursive,sans-serif;margin-right:10px;font-size:1.7rem}.calculate-button:hover{transform:scale(1.05);background-color:#ff1493}.calculate-button.disabled{background-color:#6c757d;cursor:not-allowed;opacity:.7}.score-display{margin-top:10px;font-size:1.1em;padding:10px 20px;background-color:#f597f5;border-radius:15px;font-weight:bold}.score-display .score-value{font-weight:bold;color:hotpink}